import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'fibo-seq',
  templateUrl: './fibo-seq.component.html',
  styleUrls: ['./fibo-seq.component.css']
})
export class FiboSeqComponent implements OnInit {
fibo;
res=[0,1,1];
	constructor() { }

	ngOnInit() {
  	}

  	get checkfibo(){
  		var ret=[];
  		for(var i=this.res.length-1;this.res[i]<=this.fibo;i++){
  			this.res.push(this.res[i]+this.res[i-1]);
  			}
  		for(var i=0;this.res[i]<=this.fibo;i++){
  			ret.push(this.res[i]);
  			}	
  		return JSON.stringify(ret).split(',').join(', ');
  	}
}



